interface BoxQuadrantProps {
  active?: boolean
  color: string
  label: string
  description: string
  values: string
  footer: string
  onClick?(): void
}

function BoxQuadrant(props: BoxQuadrantProps) {
  function lightenColor(hex: string, amount: number) {
    // Convert hex to RGB
    let r = parseInt(hex.slice(1, 3), 16);
    let g = parseInt(hex.slice(3, 5), 16);
    let b = parseInt(hex.slice(5, 7), 16);
  
    // Blend with white (255, 255, 255)
    r += Math.round((255 - r) * amount);
    g += Math.round((255 - g) * amount);
    b += Math.round((255 - b) * amount);
  
    return `#${r.toString(16).padStart(2, '0')}${g.toString(16).padStart(2, '0')}${b.toString(16).padStart(2, '0')}`;
  }

  return (
    <div 
      onClick={props.onClick}
      style={{
        backgroundColor: props.color,
      }}
      className={`${props.active ? 'shadow-[0px_1px_10px_1px_rgba(0,0,0,.5)] scale-[100%]' : 'scale-[92%] hover:scale-[96%]'} cursor-pointer transition transition-all flex flex-col gap-[1px] border border-[3px] border-white p-[16px_20px] text-white rounded-[12px]`}>
      <div className={`font-medium`}>
        { props.label }
      </div>
      <div 
        style={{
          color: lightenColor(props.color, .6)
        }}
        className={`text-[12px] leading-[1.6]`}>
        { props.description }
      </div>
      <div className={`text-[36px] font-medium leading-[1] mt-4`}>
        { props.values }
      </div>
      <div className={`text-[11px]`}>
        { props.footer }
      </div>
    </div>
  );
}

