import React, { forwardRef, HTMLAttributes, useEffect, useState } from "react";
import { useImperativeHandle } from "react";

export interface PopupAction {
  open(): void
  close(): void
}

interface PopupProps extends HTMLAttributes<HTMLDivElement> {
  parentClassName?: string
}

function _Popup(props: PopupProps, ref: any) {
  const [show, setShow] = useState<boolean>(false);
  useImperativeHandle(ref, () => ({
    open() {
      setShow(true);
    },
    close() {
      setShow(false);
    },
  }), []);

  if (!show) {
    return <></>;
  }

  return (
    <div 
      onMouseUp={e => setShow(false)}
      className={`fixed top-0 left-0 w-screen h-screen z-[9999] bg-[#0005] p-[12px] flex flex-col ${props.parentClassName || ''}`}>
      <div 
        {...props}
        className={`fade-in-down ${props.className || ''}`}
        onMouseUp={e => e.stopPropagation()}>
        { props.children }
      </div>
    </div>
  );
}

export const Popup = forwardRef<PopupAction, PopupProps>(_Popup);
