import React, { HTMLAttributes, useRef, useState } from "react";
import { UserFramePNG } from "../b64-images/UserFramePNG";
import { CameraBlueSVG } from "../b64-images/CameraBlueSVG";

interface ImageUploaderProps extends HTMLAttributes<HTMLDivElement> {
  containerClassName?: string
  onFileChange?(file: File): void
  imageUrl?: string
}

export function ImageUploader(props: ImageUploaderProps) {
  const uploader_ref = useRef<HTMLInputElement>(null);
  const image_ref = useRef<HTMLImageElement>(null);
  
  function inputFileChange(e: React.ChangeEvent<HTMLInputElement>) {
    if (e.target.files && e.target.files.length > 0) {
      // send file to component user
      props.onFileChange && props.onFileChange(e.target.files[0]);

      // Preview image
      (image_ref.current as any).src = URL.createObjectURL(e.target.files[0]);
      (image_ref.current as any).onload = function() {
        URL.revokeObjectURL((image_ref.current as any).src) // free memory
      }
    }
  }

  return (
    <div 
      onClick={() => uploader_ref.current?.click()}
      className={`relative h-[64px] w-[64px] cursor-pointer ${props.containerClassName || ''}`}>
      <div 
        {...props}
        className={`relative w-full h-full rounded-full overflow-hidden cursor-pointer ${props.className || ''}`}>
        <img 
          ref={image_ref}
          className={`w-full h-full object-cover`}
          src={props.imageUrl ?? UserFramePNG} />
        <input 
          hidden
          accept={"image/png,image/gif,image/jpeg"}
          ref={uploader_ref}
          onChange={inputFileChange}
          type={'file'} />
      </div>
      <img 
        className={`w-[16px] h-[16px] object-contain absolute z-[2] right-0 bottom-0`}
        src={CameraBlueSVG} />
    </div>
  );
}
