import React from "react";

export interface CascadingTreeDataType<T> {
  data: T
  children: CascadingTreeDataType<T>[]
}

interface CascadingTreeProps<T> {
  root: CascadingTreeDataType<T>
  headView(level: number): JSX.Element
  nodeView(data: T): JSX.Element
  topView?: JSX.Element
}

export function CascadingTree<T>(props: CascadingTreeProps<T>) {
  const tree_depth_level = getMaxTreeLevel(props.root);

  return (
    <div className={`flex-1 flex overflow-x-auto grid-dots-bg`}>
      <div className={`w-8 min-w-8`} />
      <div className={`flex flex-1 flex-col gap-4 py-8`}>
        { props.topView }
        <div className={`flex gap-3`}>
          { Array(tree_depth_level).fill(0).map((_, i: number) => (
            <div key={i}>
              { props.headView(i) }
            </div>
          )) }
        </div>
        <div className={`flex flex-col gap-2`}>
          <CascadingTreeItem
            tree={props.root}
            nodeView={props.nodeView} />
        </div>
      </div>
      <div className={`w-8 min-w-8`} />
    </div>
  );
}

interface CascadingTreeItemProps<T> {
  tree: CascadingTreeDataType<T>
  nodeView(data: T): JSX.Element
}
function CascadingTreeItem<T>(props: CascadingTreeItemProps<T>) {
  return (
    <div className={`flex items-start gap-3`}>
      { props.nodeView(props.tree.data) }
      <div className={`flex flex-col gap-4 mt-[36px]`}>
        {
          props.tree.children.map((item: CascadingTreeDataType<T>, i: number) => (
            <CascadingTreeItem 
              nodeView={props.nodeView}
              tree={item} />
          ))
        }
      </div>
    </div>
  );
}

function getMaxTreeLevel(tree: CascadingTreeDataType<any>, current_level: number = 1, max_depth_level = 20): number {
  if (current_level > max_depth_level) {
    throw new Error(`getMaxTreeLevel function \`getMaxTreeLevel(tree: CascadingTreeDataType<any>, current_level: number = 1, max_depth_level = 20): number\` expects tree max level = 20, if you think this is a mistake pass your custom max_depth_level number`);
  }
  if (tree.children.length == 0) {
    return current_level;
  }
  let level = current_level;
  for (const child of tree.children) {
    level = Math.max(level, getMaxTreeLevel(child, current_level + 1, max_depth_level));
  }

  return level;
}
