import React from "react";

interface NDBSimpleDoughnutChartProps {
  r: number
  stroke: number
  strokeRatio?: number
  value: number
  foregroundColor?: string
  backgroundColor?: string
}

export function NDBSimpleDoughnutChart(props: NDBSimpleDoughnutChartProps) {
  const stroke_ratio = props.strokeRatio ?? 1;
  const inner_radius_1 = props.r - props.stroke * stroke_ratio;
  const inner_radius_2 = props.r - props.stroke;

  const c = 2 * Math.PI * inner_radius_2;
  const value_a = props.value * c;
  const value_b = c - value_a;

  return (
    <svg width={props.r * 2} height={props.r * 2} viewBox={`0 0 ${props.r * 2} ${props.r * 2}`}>
      <circle
        cx={props.r} cy={props.r} r={inner_radius_1}
        fill="none"  
        stroke={props.backgroundColor ?? '#DEF0FF'}
        stroke-width={props.stroke * stroke_ratio * 2}
      />
      <circle
        cx={props.r} cy={props.r} r={inner_radius_2}
        fill="none"
        stroke={props.foregroundColor ?? '#0C87EB'} 
        stroke-width={props.stroke * 2}
        stroke-linecap="round"
        stroke-dasharray={[value_a, value_b].join(' ')}
        transform={`rotate(-90 ${props.r} ${props.r})`}
      />
    </svg>
  );
}
