import React from "react";
import { BgCardBotRigSVG } from "../b64-images/BgCardBotRigSVG";
import { DoubleCoinSVG } from "../b64-images/DoubleCoinSVG";

interface BudgetCardProps {
  label: string
  unit: string
  value: string
  bgColor: 'green' | 'blue'
}

export function BudgetCard(props: BudgetCardProps) {
  const bgColor = props.bgColor === 'blue' ? '#DEF0FF' : '#DEFFE2';
  return (
    <div 
      style={{ background: bgColor }}
      className={`w-full relative flex items-center p-8 pr-[200px] py-6 font-[Poppins] rounded-[12px] overflow-hidden`}>
      <img 
        className={`absolute self-end right-0 bottom-0 object-contain h-[88%]`}
        src={BgCardBotRigSVG} />
      <img 
        className={`absolute right-[20px] object-contain h-[60px]`}
        src={DoubleCoinSVG} />
      <div className={`z-[2] self-start flex flex-col gap-4 text-[#121D26] pb-3`}>
        <div className={`font-medium`}>
          { props.label }
        </div>
        <div className={`flex items-start gap-2`}>
          <div className={`text-[#8DA6C4] mt-[6px] font-medium`}>{ props.unit }</div>
          <div className={`text-[32px] font-semibold`}>{ props.value }</div>
        </div>
      </div>
    </div>
  );
}
