import React from "react";

interface GeneralTabProps {
  v2?: boolean
  tabs: string[]
  active: string
  onTabChange(tab: string): void
  px?: number
}

export function GeneralTab(props: GeneralTabProps) {
  if (props.v2) {
    return (
      <div 
        style={{ paddingLeft: props.px ?? 0, paddingRight: props.px ?? 0 }}
        className={`flex gap-4 overflow-x-auto font-[Poppins] border-b border-b-[2px] border-b-[#EAEFF5]`}>
        { props.tabs.map((tab, i) => {
          const active = props.active == tab;
  
          return (
            <div 
              onClick={() => props.onTabChange(tab)}
              className={`flex flex-col cursor-pointer`}>
              <div className={`text-[#0066B9] flex-1 flex items-center text-center p-[5px_16px] text-[14px] ${active ? 'text-[#0066B9] font-medium text-[17px]' : 'text-[#8DA6C4]'}`}>
                { tab }
              </div>
              <div className={active ? 'bg-[#0066B9] h-[2px]' : 'bg-transparent h-[2px]'} />
            </div>
          );
        }) }
      </div>
    );
  }
  
  return (
    <div className={`flex overflow-x-auto font-[Poppins] border-b border-b-px border-b-[#EAEFF5]`}>
      { props.tabs.map((tab, i) => {
        const active = props.active == tab;

        return (
          <div 
            onClick={() => props.onTabChange(tab)}
            className={`flex flex-col flex-1 cursor-pointer`}>
            <div className={`text-[#0066B9] text-center p-[16px] ${active ? 'text-[#0066B9] font-medium' : 'text-[#8DA6C4]'}`}>
              { tab }
            </div>
            <div className={active ? 'bg-[#0066B9] h-[2px]' : 'bg-transparent h-[2px]'} />
          </div>
        );
      }) }
    </div>
  );
}
