import React, { HTMLAttributes } from "react";
import { SuccessSVG } from "../b64-images/SuccessSVG";
import { Button } from "./Button";

interface SuccessModalProps extends HTMLAttributes<HTMLDivElement> {
  title: string
  description?: string
  onClose?(): void
}

export function SuccessModal(props: SuccessModalProps) {
  return (
    <div 
      {...props}
      style={{
        background: 'linear-gradient(138deg, rgba(10, 133, 233, 1) 0%, rgba(30, 231, 253, 1) 100%)',
        ...props.style
      }}
      className={`rounded-[24px] p-[4px] font-[Poppins] ${props.className || ''}`}>
      <div className={`rounded-[20px] p-[32px_24px] flex flex-col gap-4 bg-white items-center`}>
        <img 
          className={`h-[150px] object-contain`}
          src={SuccessSVG} />
        <div className={`text-[24px] font-bold text-center`}>
          { props.title }
        </div>
        { props.description && <div className={`text-center text-[15px] text-[#8DA6C4]`}>
          { props.description }
        </div> }
        <Button 
          onClick={props.onClose}
          className={`!border-[#8DA6C4] !text-[#8DA6C4] !bg-transparent mt-10`}
          smaller>
          Close
        </Button>
      </div>
    </div>
  );
}
