import React from "react";
import { CheckSVG } from "../b64-images/CheckSVG";

interface VerticalStepProps {
  activeIndex: number
  items?: string[][]
}

export function VerticalStep(props: VerticalStepProps) {
  const list_item = props.items ?? [
    [
      'Location',
      'List data location'
    ],
    [
      'Job Profile',
      'List data job profile'
    ],
    [
      'Employee',
      'List data employee'
    ],
    [
      'Organization Structure',
      'Detail organizations structure'
    ],
  ];

  return (
    <div className={`flex flex-col gap-[2px] font-[Poppins]`}>
      {
        list_item.map(([label, desc], i) => {
          const is_latest = i == (list_item.length - 1);
          const is_image = i < (props.activeIndex - 1);
          const is_active_index = i == (props.activeIndex - 1);
          return (
            <div 
              key={i}
              className={`flex flex-col gap-[2px]`}>
              <div className={`flex items-center gap-[8px]`}>
                <div className={`w-[32px] h-[32px] flex items-center justify-center rounded-full border border-px border-[#0066B9] text-[#0066B9] font-medium`}>
                  { is_image ? <img src={CheckSVG} className={`w-full h-full`} /> : String(i + 1) }
                </div>
                <div className={`flex flex-col gap-[0px]`}>
                  <div className={`${is_active_index ? 'text-[#121D26]' : 'text-[#8DA6C4]'} font-medium`}>
                    { label }
                  </div>
                  <div className={`text-[#8DA6C4]`}>
                    { desc }
                  </div>
                </div>
              </div>
              { (!is_latest) && <div className={`${is_image ? 'bg-[#0066B9]' : 'bg-transparent'} h-[32px] w-[2px] ml-[15px] my-[-4px]`} /> }
            </div>
          );
        })
      }
    </div>
  );
}
