import React, { InputHTMLAttributes, SelectHTMLAttributes } from "react";
import { getIcon, InputIconType } from "./InputIconType";

interface InputSelectProps extends SelectHTMLAttributes<HTMLSelectElement> {
  containerClassName?: string
  icon?: InputIconType
  v2?: boolean
  v3?: boolean
}

export function InputSelect(props: InputSelectProps) {
  let version: 'v1' | 'v2' | 'v3' = 'v1';
  if (props.v2) {
    version = 'v2';
  }
  if (props.v3) {
    version = 'v3';
  }

  let icon = props.icon ? getIcon(props.icon) : null;

  return (
    <div className={`relative flex items-center ${props.containerClassName || ''}`}>
      { version == 'v1' && <select
        {...props}
        className={`border border-[1.5px] text-[13px] bg-[#F9FAFB] border-[#EAEFF5] w-full p-[12px_12px] rounded-[12px] ${icon ? 'pl-[48px]' : ''} ${props.className || ''}`} /> }
      { version == 'v2' && <select
        {...props}
        className={`border border-[1.5px] text-[13px] placeholder-[#8DA6C4] border-[#8DA6C4] p-[12px_12px] rounded-[12px] ${icon ? 'pl-[48px]' : ''} ${props.className || ''}`} /> }
      { version == 'v3' && <select
        {...props}
        className={`border border-[1.5px] text-[13px] bg-white border-[#EAEFF5] p-[12px_12px] rounded-[12px] ${icon ? 'pl-[48px]' : ''} ${props.className || ''}`} /> }
      { icon && <img src={icon} className={`absolute w-[17px] h-[17px] ml-[18px] object-contain`} /> }
    </div>
  );
}
