import React, { useId } from "react";
import { v4 } from "uuid";

interface NDBDoughnutChartProps {
  r: number
  stroke: number
  strokeRatio?: number
  value: number
  blue?: boolean
  yellow?: boolean
  red?: boolean
  d1BackgroundColor?: string
  d2BackgroundColorG1?: string
  d2BackgroundColorG2?: string
}

export function NDBDoughnutChart(props: NDBDoughnutChartProps) {
  const stroke_ratio = props.strokeRatio ?? .5;
  const inner_radius_1 = props.r - props.stroke * stroke_ratio;
  const inner_radius_2 = props.r - props.stroke;
  const linear_gd_id = useId();

  let d1_bg = props.d1BackgroundColor ?? '#CCC';
  let d2_bg_g1 = props.d2BackgroundColorG1 ?? '#01E400';
  let d2_bg_g2 = props.d2BackgroundColorG1 ?? '#FEFF01';

  if (props.blue) {
    d1_bg = props.d1BackgroundColor ?? '#C6E5FF';
    d2_bg_g1 = props.d2BackgroundColorG1 ?? '#0066B9';
    d2_bg_g2 = props.d2BackgroundColorG1 ?? '#01CFBE';
  }

  if (props.yellow) {
    d1_bg = props.d1BackgroundColor ?? '#FFF6DD';
    d2_bg_g1 = props.d2BackgroundColorG1 ?? '#FFCA43';
    d2_bg_g2 = props.d2BackgroundColorG1 ?? '#FCE9B9';
  }

  if (props.red) {
    d1_bg = props.d1BackgroundColor ?? '#FFEFEF';
    d2_bg_g1 = props.d2BackgroundColorG1 ?? '#FF4343';
    d2_bg_g2 = props.d2BackgroundColorG1 ?? '#FFE2E2';
  }

  const c = 2 * Math.PI * inner_radius_2;
  const value_a = props.value * c;
  const value_b = c - value_a;

  return (
    <svg width={props.r * 2} height={props.r * 2} viewBox={`0 0 ${props.r * 2} ${props.r * 2}`}>
      <circle
        cx={props.r} cy={props.r} r={inner_radius_1}
        fill="none"  
        stroke={d1_bg}
        stroke-width={props.stroke * stroke_ratio * 2}
      />
      <linearGradient id={linear_gd_id} x1="0" y1="0" x2="1" y2="1">
       <stop offset="0%" stop-color={d2_bg_g1}></stop>
       <stop offset="100%" stop-color={d2_bg_g2}></stop>
      </linearGradient>
      <circle
        cx={props.r} cy={props.r} r={inner_radius_2}
        fill="none"
        stroke={`url(#${linear_gd_id})`} 
        stroke-width={props.stroke * 2}
        stroke-linecap="round"
        stroke-dasharray={[value_a, value_b].join(' ')}
        transform={`rotate(-90 ${props.r} ${props.r})`}
      />
    </svg>
  );
}
