import React from "react";
import { IconBoxPeopleSVG } from "../b64-images/IconBoxPeopleSVG";
import { IconBoxGroupSVG } from "../b64-images/IconBoxGroupSVG";
import { IconBoxGlobeSVG } from "../b64-images/IconBoxGlobeSVG";
import { IconBoxGenZSVG } from "../b64-images/IconBoxGenZSVG";
import { IconBoxGenMSVG } from "../b64-images/IconBoxGenMSVG";
import { IconBoxGenBSVG } from "../b64-images/IconBoxGenBSVG";
import { MaleSVG } from "../b64-images/MaleSVG";
import { FemaleSVG } from "../b64-images/FemaleSVG";

export interface TECValueType1 {
  type: 1
  value: string
}
export interface TECValueType2 {
  type: 2
  value: string
  unit: string
}
export interface TECBottomType0 {
  type: 0
}
export interface TECBottomType1 {
  type: 1
  men: string
  woman: string
}
export interface TECBottomType2 {
  type: 2
  subdivision: string
}

interface TotalEmployeeCardProps {
  value: TECValueType1 | TECValueType2
  bottom: TECBottomType0 | TECBottomType1 | TECBottomType2
  icon: 'people' | 'group' | 'globe' | 'genz' | 'genm' | 'genb'
}

export function TotalEmployeeCard(props: TotalEmployeeCardProps) {
  let icon: string = '';
  switch (props.icon) {
    case "people": icon = IconBoxPeopleSVG; break;
    case "group": icon = IconBoxGroupSVG; break;
    case "globe": icon = IconBoxGlobeSVG; break;
    case "genz": icon = IconBoxGenZSVG; break;
    case "genm": icon = IconBoxGenMSVG; break;
    case "genb": icon = IconBoxGenBSVG; break;
  }
  return (
    <div className={`flex justify-between font-[Poppins] border border-[1.5px] border-[#F1F5F9] rounded-[12px] p-[12px_18px]`}>
      <div className={`flex flex-col gap-1`}>
        <div className={`text-[#8DA6C4] text-[14px] font-medium`}>
          Total Employee
        </div>
        { props.value.type == 1 && <div className={`text-[30px] font-semibold`}>
          { props.value.value }
        </div> }
        { props.value.type == 2 && <div className={`flex items-end gap-2`}>
          <div className={`text-[30px] font-semibold`}>
            { props.value.value }
          </div>
          <div className={`mb-2`}>
            { props.value.unit }
          </div>
        </div> }
        { props.bottom.type === 0 && <div className={`flex-1`} /> }
        { props.bottom.type === 1 && <div className={`flex items-center gap-4 text-[14px]`}>
          <div className={`flex items-center gap-2`}>
            <img 
              className={`w-[14px] h-[14px] object-contain`}
              src={MaleSVG} />
            <div className={`flex items-center gap-1`}>
              <span className={`text-[#8DA6C4]`}>Men</span>
              <span className={`font-medium`}>{ props.bottom.men }</span>
            </div>
          </div>
          <div className={`h-[20px] w-[1.5px] bg-[#F1F5F9]`} />
          <div className={`flex items-center gap-2`}>
            <img 
              className={`w-[14px] h-[14px] object-contain`}
              src={FemaleSVG} />
            <div className={`flex items-center gap-1`}>
              <span className={`text-[#8DA6C4]`}>Woman</span>
              <span className={`font-medium`}>{ props.bottom.woman }</span>
            </div>
          </div>
        </div> }
        { props.bottom.type === 2 && <div className={`text-[14px]`}>
          <span className={`text-[#8DA6C4]`}>with </span>
          <span className={`font-semibold`}>{ props.bottom.subdivision }</span>
          <span className={`text-[#8DA6C4]`}> Sub-division</span>
        </div> }
      </div>
      <img 
        className={`w-[32px] h-[32px] object-contain`}
        src={icon} />
    </div>
  );
}
