const OKRs_svg = 'data:image/svg+xml;base64,';
const TrainingEvent_svg = 'data:image/svg+xml;base64,';
const Library_svg = 'data:image/svg+xml;base64,';
const TrainingBudget_svg = 'data:image/svg+xml;base64,';
