import { TrendingUpIcon } from "lucide-react";
import React from "react";

interface NDBFinancialCardProps {
  mainColor: 'blue' | 'green' | 'yellow'
  miniInfoType: 'value' | 'change'
  label: string
  value: string
  sublabel: string
  subvalue: string
  miniInfoValue: string
  forecast: string
}

export function NDBFinancialCard(props: NDBFinancialCardProps) {
  let main_color = '#2F9DF6';
  switch (props.mainColor) {
    case "blue": main_color = '#2F9DF6'; break;
    case "green": main_color = '#2FF688'; break;
    case "yellow": main_color = '#EBC52F'; break;
  }

  return (
    <div className={`border border-[2px] border-[#F1F5F9] rounded-[8px] flex-1 flex flex-col`}>
      <div className={`p-[12px_15px] flex flex-col gap-2 flex-1`}>
        <div className={`text-[14px]`}>
          { props.label }
        </div>
        <div 
          style={{
            color: main_color
          }}
          className={`text-[20px] font-medium mt-2`}>
          { props.value }
        </div>
        <div className={`flex items-center justify-between`}>
          <div className={`text-[12px]`}>
            <span className={`text-[#8DA6C4]`}>{ props.sublabel }:</span> { props.subvalue }
          </div>
          { props.miniInfoType === 'value' && <div className={`bg-[#E4FFF0] text-[#00E05C] text-[12px] p-[2px_9px] rounded-[6px]`}>
            { props.miniInfoValue }
          </div> }
          { props.miniInfoType === 'change' && <div className={`flex items-center gap-2 text-[12px]`}>
            <TrendingUpIcon className={`text-[#00E05C]`} size={16} />
            <span className={`text-[#00E05C]`}>
              { props.miniInfoValue }
            </span>
            <span className={`text-[#8DA6C4]`}>
              from before
            </span>
          </div> }
        </div>
      </div>
      <div className={`p-[12px_15px] border-t border-t-[2px] border-t-[#F1F5F9] bg-[#F9FAFB] flex items-center gap-2 text-[12px]`}>
        <img 
          className={`w-[16px] object-contain`}
          src={Forecast_svg} />
        Forecast: { props.forecast }
      </div>
    </div>
  );
}

const Forecast_svg = 'data:image/svg+xml;base64,';
