import React, { Fragment } from "react";

interface NDBSimpleHexagonChartProps {
  r: number

  //       bg,            line/dot,      p1,     p2,     p3,     p4,     p5,     p6
  values: [string | null, string | null, number, number, number, number, number, number][]

  foregroundColor?: string
  bgStrip1?: string
  bgStrip2?: string
  bgStroke?: string
}

export function NDBSimpleHexagonChart(props: NDBSimpleHexagonChartProps) {
  const stroke_width = 1;
  const cirlce_radius = 4;
  const offset_x = HF.minX(props.r) - (stroke_width / 2) - cirlce_radius;
  const offset_y = HF.minY(props.r) - (stroke_width / 2) - cirlce_radius;
  const w = HF.width(props.r) + stroke_width + 2 * cirlce_radius;
  const h = HF.height(props.r) + stroke_width + 2 * cirlce_radius;

  const r5 = 5 * props.r / 5;
  const r4 = 4 * props.r / 5;
  const r3 = 3 * props.r / 5;
  const r2 = 2 * props.r / 5;
  const r1 = 1 * props.r / 5;

  const line_14: [[number, number], [number, number]] = [HF.point(r5, 1, offset_x, offset_y), HF.point(r5, 4, offset_x, offset_y)];
  const line_25: [[number, number], [number, number]] = [HF.point(r5, 2, offset_x, offset_y), HF.point(r5, 5, offset_x, offset_y)];
  const line_36: [[number, number], [number, number]] = [HF.point(r5, 3, offset_x, offset_y), HF.point(r5, 6, offset_x, offset_y)];

  return (
    <svg width={w} height={h} viewBox={`0 0 ${w} ${h}`}>
      <path 
        d={[
          'M', ...HF.point(r5, 1, offset_x, offset_y),
          'L', ...HF.point(r5, 2, offset_x, offset_y),
          'L', ...HF.point(r5, 3, offset_x, offset_y),
          'L', ...HF.point(r5, 4, offset_x, offset_y),
          'L', ...HF.point(r5, 5, offset_x, offset_y),
          'L', ...HF.point(r5, 6, offset_x, offset_y),
          'Z'
        ].join(' ')}
        fill={props.bgStrip1 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />
      <path 
        d={[
          'M', ...HF.point(r4, 1, offset_x, offset_y),
          'L', ...HF.point(r4, 2, offset_x, offset_y),
          'L', ...HF.point(r4, 3, offset_x, offset_y),
          'L', ...HF.point(r4, 4, offset_x, offset_y),
          'L', ...HF.point(r4, 5, offset_x, offset_y),
          'L', ...HF.point(r4, 6, offset_x, offset_y),
          'Z'
        ].join(' ')}
        fill={props.bgStrip2 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />
      <path 
        d={[
          'M', ...HF.point(r3, 1, offset_x, offset_y),
          'L', ...HF.point(r3, 2, offset_x, offset_y),
          'L', ...HF.point(r3, 3, offset_x, offset_y),
          'L', ...HF.point(r3, 4, offset_x, offset_y),
          'L', ...HF.point(r3, 5, offset_x, offset_y),
          'L', ...HF.point(r3, 6, offset_x, offset_y),
          'Z'
        ].join(' ')}
        fill={props.bgStrip1 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />
      <path 
        d={[
          'M', ...HF.point(r2, 1, offset_x, offset_y),
          'L', ...HF.point(r2, 2, offset_x, offset_y),
          'L', ...HF.point(r2, 3, offset_x, offset_y),
          'L', ...HF.point(r2, 4, offset_x, offset_y),
          'L', ...HF.point(r2, 5, offset_x, offset_y),
          'L', ...HF.point(r2, 6, offset_x, offset_y),
          'Z'
        ].join(' ')}
        fill={props.bgStrip2 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />
      <path 
        d={[
          'M', ...HF.point(r1, 1, offset_x, offset_y),
          'L', ...HF.point(r1, 2, offset_x, offset_y),
          'L', ...HF.point(r1, 3, offset_x, offset_y),
          'L', ...HF.point(r1, 4, offset_x, offset_y),
          'L', ...HF.point(r1, 5, offset_x, offset_y),
          'L', ...HF.point(r1, 6, offset_x, offset_y),
          'Z'
        ].join(' ')}
        fill={props.bgStrip1 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />
      <path 
        d={[
          'M', ...line_14[0],
          'L', ...line_14[1],
        ].join(' ')}
        fill={props.bgStrip1 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />
      <path 
        d={[
          'M', ...line_25[0],
          'L', ...line_25[1],
        ].join(' ')}
        fill={props.bgStrip1 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />
      <path 
        d={[
          'M', ...line_36[0],
          'L', ...line_36[1],
        ].join(' ')}
        fill={props.bgStrip1 ?? '#EEE'}
        stroke={props.bgStroke ?? '#999'}
        strokeWidth={stroke_width} />

      {/* Hexagon values */}
      {
        props.values.map(([bg, line_dot, p1, p2, p3, p4, p5, p6], i: number) => (
          <path 
            key={i}
            d={[
              'M', ...HF.point(Math.min(props.r, Math.max(0, p1)), 1, offset_x, offset_y),
              'L', ...HF.point(Math.min(props.r, Math.max(0, p2)), 2, offset_x, offset_y),
              'L', ...HF.point(Math.min(props.r, Math.max(0, p3)), 3, offset_x, offset_y),
              'L', ...HF.point(Math.min(props.r, Math.max(0, p4)), 4, offset_x, offset_y),
              'L', ...HF.point(Math.min(props.r, Math.max(0, p5)), 5, offset_x, offset_y),
              'L', ...HF.point(Math.min(props.r, Math.max(0, p6)), 6, offset_x, offset_y),
              'Z'
            ].join(' ')}
            fill={bg ?? '#EEE'}
            stroke={line_dot ?? '#999'}
            strokeWidth={stroke_width} />
        ))
      }

      {/* Circle values */}
      {
        props.values.map(([bg, line_dot, p1, p2, p3, p4, p5, p6], i: number) => {
          const cp1 = HF.point(Math.min(props.r, Math.max(0, p1)), 1, offset_x, offset_y);
          const cp2 = HF.point(Math.min(props.r, Math.max(0, p2)), 2, offset_x, offset_y);
          const cp3 = HF.point(Math.min(props.r, Math.max(0, p3)), 3, offset_x, offset_y);
          const cp4 = HF.point(Math.min(props.r, Math.max(0, p4)), 4, offset_x, offset_y);
          const cp5 = HF.point(Math.min(props.r, Math.max(0, p5)), 5, offset_x, offset_y);
          const cp6 = HF.point(Math.min(props.r, Math.max(0, p6)), 6, offset_x, offset_y);
          return (
            <Fragment key={i}>
              <circle key={`${i}-1`} cx={cp1[0]} cy={cp1[1]} r={cirlce_radius} fill={line_dot ?? '#EEE'} stroke={'#FFF'} />
              <circle key={`${i}-2`} cx={cp2[0]} cy={cp2[1]} r={cirlce_radius} fill={line_dot ?? '#EEE'} stroke={'#FFF'} />
              <circle key={`${i}-3`} cx={cp3[0]} cy={cp3[1]} r={cirlce_radius} fill={line_dot ?? '#EEE'} stroke={'#FFF'} />
              <circle key={`${i}-4`} cx={cp4[0]} cy={cp4[1]} r={cirlce_radius} fill={line_dot ?? '#EEE'} stroke={'#FFF'} />
              <circle key={`${i}-5`} cx={cp5[0]} cy={cp5[1]} r={cirlce_radius} fill={line_dot ?? '#EEE'} stroke={'#FFF'} />
              <circle key={`${i}-6`} cx={cp6[0]} cy={cp6[1]} r={cirlce_radius} fill={line_dot ?? '#EEE'} stroke={'#FFF'} />
            </Fragment>
          )}
        )
      }
    </svg>
  );
}

namespace HF {

  //      4 +
  //      /   \
  //  5 +       + 3
  //    |       |
  //  6 +       + 2
  //      \   /
  //        + 1
  export type PointPosition = 1 | 2 | 3 | 4 | 5 | 6;
  export const point_angle = {
    1: Math.PI / 2,
    2: Math.PI / 6,
    3: -Math.PI / 6,
    4: -Math.PI / 2,
    5: 5 * - Math.PI / 6,
    6: 5 * Math.PI / 6,
  };

  export function point(radius: number, position: PointPosition, offset_x: number = 0, offset_y: number = 0): [number, number] {
    return [
      Math.fround(+(Math.cos(point_angle[position]) * radius).toFixed(10)) - offset_x,
      Math.fround(+(Math.sin(point_angle[position]) * radius).toFixed(10)) - offset_y
    ];
  }

  export function minX(radius: number) {
    return Math.fround(+(Math.cos(point_angle[6]) * radius).toFixed(10));
  }

  export function minY(radius: number) {
    return Math.fround(+(Math.sin(point_angle[4]) * radius).toFixed(10));
  }

  export function width(radius: number) {
    return 2 * Math.abs(minX(radius));
  }

  export function height(radius: number) {
    return 2 * Math.abs(minY(radius));
  }
}
