import React, { HTMLAttributes } from "react";
import { NDNavbarOnboarding, NDNavbarOnboardingProps } from "./NDNavbarOnboarding";

interface NDTemplateOnboardingProps extends HTMLAttributes<HTMLDivElement> {
  navbar: NDNavbarOnboardingProps
  stickyRightChildren?: JSX.Element
  children?: any
}

export function NDTemplateOnboarding(props: NDTemplateOnboardingProps) {
  const is_height_screen_full = Boolean(props.stickyRightChildren);
  return (
    <div  
      {...props}
      className={`flex bg-[#FAFDFF] min-h-screen ${is_height_screen_full ? 'h-screen overflow-hidden' : ''} ${props.className || ''}`}>
      <div className={`flex-1 flex flex-col relative`}>
        <div className={`sticky top-0  z-[998]`}>
          <NDNavbarOnboarding {...props.navbar} />
        </div>
        <div className={`flex flex-1 h-0`}>
          <div className={`flex-1 w-0 overflow-auto`}>
            { props.children }
          </div>
          { is_height_screen_full && <div className={`sticky right-0 h-full`}>
            { props.stickyRightChildren }
          </div> }
        </div>
      </div>
    </div>
  );
}
