import React, { useLayoutEffect, useRef, useState } from "react";
import { NDSidebar, NDSidebarOptions } from "./NDSidebar";
import { NDNavbar } from "./NDNavbar";
import { NDInnerSidebar } from "./NDInnerSidebar";

interface NDTemplateProps {
  active: NDSidebarOptions
  children?: any
  rightStickyElement?: JSX.Element
}

export function NDTemplate(props: NDTemplateProps) {
  const nav_ref = useRef<HTMLDivElement>(null);
  const second_sticky = useRef<HTMLDivElement>(null);
  const right_sticky = useRef<HTMLDivElement>(null);
  const [is_collapse, setIsCollapse] = useState<boolean>(false);
  const [hover_sidebar, setHoverSidebar] = useState<NDSidebarOptions>(props.active);

  useLayoutEffect(() => {
    if (nav_ref && nav_ref.current) {
      const { height } = nav_ref.current.getBoundingClientRect();
      (second_sticky.current as any).style.top = `${height}px`;
      (second_sticky.current as any).style.height = `${window.innerHeight - height}px`;
      if (right_sticky.current) {
        (right_sticky.current as any).style.top = `${height}px`;
        (right_sticky.current as any).style.height = `${window.innerHeight - height}px`;
      }
    }
  }, []);

  return (
    <div className={`flex`}>
      <div className={``}>
        <div className={`sticky top-0 left-0 z-[999]`}>
          <NDSidebar 
            onHover={setHoverSidebar}
            active={hover_sidebar} />
        </div>
      </div>
      <div className={`flex-1 flex flex-col relative`}>
        <div 
          ref={nav_ref}
          className={`sticky top-0  z-[998]`}>
          <NDNavbar />
        </div>
        <div className={`flex`}>
          <div className={`relative`}>
            <div 
              ref={second_sticky}
              style={{
                marginLeft: is_collapse ? -(second_sticky.current?.clientWidth ?? 0) : 0
              }}
              className={`p-2 py-6 sticky top-0 bg-[#F8FCFF] h-full border border-[#EAEFF5] flex flex-col gap-4 transition transition-all`}>
              <NDInnerSidebar active={hover_sidebar} />
              <div 
                onClick={() => setIsCollapse(!is_collapse)}
                className={`absolute right-0 ${is_collapse ? 'mr-[-30px]' : 'mr-[-15px]'} top-[18px] cursor-pointer z-[9999] transition transition-all`}>
                <img 
                  className={`min-w-[30px] min-h-[30px] w-[30px] h-[30px] object-contain`}
                  src={'data:image/svg+xml;base64,'} />
              </div>
            </div>
          </div>
          <div 
            onMouseOverCapture={() => {
              if (hover_sidebar !== props.active) {
                setHoverSidebar(props.active);
              }
            }}
            className={`flex-1 w-0`}>
            { props.children }
          </div>
          { props.rightStickyElement && <div className={`relative`}>
            <div 
              ref={right_sticky}
              className={`sticky top-0 bg-[#F8FCFF] h-full border border-[#EAEFF5]`}>
              { props.rightStickyElement }
            </div>
          </div> }
        </div>
      </div>
    </div>
  );
}
