import React from "react";

interface OrganizationTreeProps {
  root: TreeViewData
}

export function OrganizationTree(props: OrganizationTreeProps) {
  return (
    <div className={`border border-[1.5px] border-[#EAEFF5] bg-[#F9FAFB] flex flex-col rounded-[12px] flex flex-col items-center justify-center w-full h-full min-w-full min-h-full font-[Poppins] overflow-auto`}>
      <div className={`min-h-[24px] h-[24px] w-full`} />
      <div className={`flex`}>
        <div className={`min-w-[24px] w-[24px] h-full`}>&nbsp;</div>
        <TreeView data={props.root} />
        <div className={`min-w-[24px] w-[24px] h-full`}>&nbsp;</div>
      </div>
      <div className={`min-h-[24px] h-[24px] w-full`} />
    </div>
  );
}

interface TreeCardProps {
  data: TreeViewData
  w: number
}
function TreeCard(props: TreeCardProps) {
  return (
    <div 
      style={{ width: props.w }}
      className={`relative bg-white border border-px border-[#EAEFF5] rounded-[10px] p-[10px_14px]`}>
      <div className={`flex gap-2 items-center mb-[4px]`}>
        <img
          className={`w-[36px] h-[36px] object-cover rounded-full`}
          src={props.data.photo} />
        <div className={`flex flex-col gap-0`}>
          <div className={`font-medium`}>
            { props.data.name }
          </div>
          <div className={`text-[#8DA6C4] font-light text-[14px] mt-[-2px]`}>
            { props.data.title }
          </div>
        </div>
      </div>
      <div 
        style={{
          borderBottomLeftRadius: 10,
          borderBottomRightRadius: 10,
        }}
        className={`absolute bottom-0 left-0 w-full h-[4px] bg-[#0066B9] z-[998] flex justify-center items-center`} />
      <div  className={`absolute z-[999] bottom-[-6px] left-0 flex justify-center w-full`}>
        <div className={`rounded-full border border-[3.5px] border-[#0066B9] w-[16px] h-[16px] bg-white`} />
      </div>
    </div>
  );
}

export interface TreeViewData {
  level?: number
  name: string
  photo: string
  title: string
  childs?: TreeViewData[]
}

interface TreeViewProps {
  data: TreeViewData
}

function TreeView(props: TreeViewProps) {
  const config = {
    card_width: 250,
    card_spaces: 50,
    vertical_height: 25,
    line_stroke: 2
  };

  const total_children = props.data.childs?.length ?? 0;
  const horizontal_width = total_children === 0
    ? 0
    : (total_children - 1) * (config.card_width + config.card_spaces);

  return (
    <div 
      data-x={JSON.stringify({ level: props.data.level ?? 0, total_c: props.data.childs?.length ?? 0 })} 
      className={`flex flex-col items-center`}>
      <TreeCard 
        data={props.data}
        w={config.card_width} />
      { (props.data.childs && (props.data.childs?.length ?? 0) > 0) && <>
        <div style={{
          height: config.vertical_height * 2 + config.line_stroke,
          minHeight: config.vertical_height * 2 + config.line_stroke,
          width: '100%'
        }}></div>
        <div 
          style={{
            gap: config.card_spaces
          }}
          className={`flex`}>
          {
            props.data.childs.map((tv_data, x: number) => (
              <TreeView 
                data={{
                  ...tv_data,
                  level: (props.data.level ?? 0) + 1
                }}
                key={x} />
            ))
          }
        </div>
      </> }
    </div>
  );
}
