import React from "react";
import { EditSVG } from "../b64-images/EditSVG";
import { TrashSVG } from "../b64-images/TrashSVG";

interface TableActionsProps {
  onEdit?(): void
  onDelete?(): void
}

export function TableActions(props: TableActionsProps) {
  return (
    <div className={`flex items-center gap-3`}>
      <img
        onClick={props.onEdit}
        className={`w-4 object-contain cursor-pointer`}
        src={EditSVG} />
      <img
        onClick={props.onDelete}
        className={`w-4 object-contain cursor-pointer`}
        src={TrashSVG} />
    </div>
  );
}
