import React from "react";

interface SideOKRCardProps {
  title: string
  badge?: JSX.Element
  description: string
  value: number
  onSubmit?(): void
}

export function SideOKRCard(props: SideOKRCardProps) {
  return (
    <div className={`flex flex-col gap-[4px] p-[8px_12px] rounded-[12px] bg-[#F9FBFD] border border-[1.5px] border-zinc-200`}>
      <div className={`flex items-center justify-between`}>
        <div className={`text-[14px] font-bold`}>
          { props.title }
        </div>
        { props.badge }
      </div>
      <div className={`text-zinc-500 py-[6px]`}>
        { props.description }
      </div>
      <div className={`bg-blue-100 h-[5px] overflow-hidden rounded-full`}>
        <div 
          style={{
            width: props.value
          }}
          className={`bg-blue-500 h-full rounded-full`} />
      </div>
      <div>
        { (+props.value).toFixed(2) }%
      </div>
      <div 
        onClick={() => props.onSubmit && props.onSubmit()}
        className={`flex items-center justify-center border border-[1.5px] border-[#13639E] text-[#13639E] rounded-[6px] py-[4px] gap-[4px]`}>
        <img 
          className={`w-[15px] h-[15px] object-contain`}
          src={'https://dashboard.terampil.biz/icons2/attachment.svg'} />
        <div className={`text-[13px] font-bold`}>
          Submit Progress
        </div>
      </div>
    </div>
  );
}
