import React from "react";
import { NDBDoughnutChart } from "./NDBDoughnutChart";
import { AlertTriangle, AlertTriangleIcon, ArrowBigUpIcon, CheckIcon, SquareCheckBigIcon, TrendingDownIcon, TrendingUpIcon, TriangleAlert, TriangleAlertIcon } from "lucide-react";

interface NDBCardDoughnutProps {
  title: string
  value: number
  mode: 'blue' | 'yellow' | 'red'
}

export function NDBCardDoughnut(props: NDBCardDoughnutProps) {
  return (
    <div className={`flex flex-col font-[Poppins] border border-[2px] border-[#F1F5F9] rounded-[8px]`}>
      <div className={`flex flex-col gap-3 p-[14px_20px]`}>
        <div className={`font-medium`}>
          { props.title }
        </div>
        <div className={`flex gap-4 items-end`}>
          <div className={`flex-1 flex`}>
            <div className={`flex items-center justify-center`}>
              <NDBDoughnutChart
                value={props.value}
                r={68}
                stroke={12}
                strokeRatio={.6}
                blue={props.mode === 'blue'}
                yellow={props.mode === 'yellow'}
                red={props.mode === 'red'} />
              { props.mode === 'blue' && <div className={`absolute text-[#0066B9] font-semibold text-[30px]`}>
                { (props.value * 100).toFixed(0) }%
              </div> }
              { props.mode === 'yellow' && <div className={`absolute text-[#FFCA43] font-semibold text-[30px]`}>
                { (props.value * 100).toFixed(0) }%
              </div> }
              { props.mode === 'red' && <div className={`absolute text-[#FF4343] font-semibold text-[30px]`}>
                { (props.value * 100).toFixed(0) }%
              </div> }
            </div>
          </div>
          <div className={`flex flex-col gap-[1px] mb-2`}>
            { props.mode == 'blue' && <div className={`flex gap-2 items-center text-[#00E05C] text-[15px]`}>
              <TrendingUpIcon size={18} />
              { (.2 * 100).toFixed(0) }%
            </div> }
            { props.mode == 'yellow' && <div className={`flex gap-2 items-center text-[#FFCA43] text-[15px]`}>
              <TrendingDownIcon size={18} />
              { (.2 * 100).toFixed(0) }%
            </div> }
            { props.mode == 'red' && <div className={`flex gap-2 items-center text-[#FF4343] text-[15px]`}>
              <TrendingDownIcon size={18} />
              { (.2 * 100).toFixed(0) }%
            </div> }
            <div className={`text-[#8DA6C4] text-[13px]`}>
              from
            </div>
            <div className={`text-[#8DA6C4] text-[13px]`}>
              Q1 2023
            </div>
          </div>
        </div>
      </div>
      <div className={`border-t border-t-[2px] border-t-[#F1F5F9] p-[12px_20px] flex items-center gap-2 text-[12px] h-[55px]`}>
        { props.mode === 'blue' && <SquareCheckBigIcon className={`text-[#00E05C]`} size={20} /> }
        { props.mode === 'yellow' && <AlertTriangleIcon className={`text-[#FFCA43]`} size={20} /> }
        { props.mode === 'red' && <AlertTriangleIcon className={`text-[#FF4343]`} size={20} /> }
        { props.mode === 'blue' && <div className={'text-[#121D26]'}>Good</div> }
        { props.mode === 'yellow' && <div className={'text-[#121D26]'}>The risk is moderate and this trend is likely to continue.</div> }
        { props.mode === 'red' && <div className={'text-[#FF4343]'}>Risk of missing target by 12% if trend continues.</div> }
      </div>
    </div>
  );
}
