import React from "react";

interface NDBMainSummaryProps {}

export function NDBMainSummary(props: NDBMainSummaryProps) {
  return (
    <div className={`flex rounded-full border border-[2px] border-[#EAEFF5]`}>
      {
        [
          ['Performance Progress', '80%', true],
          ['Learning Progress', '80%', false],
        ].map(([label, progress, active], i: number) => (
          <div 
            key={i}
            className={`${active ? 'bg-[#2C9CF7] text-white font-medium' : 'text-[#8DA6C4]'} text-[14px] rounded-full m-[-1px] flex-1 p-[13px_28px] flex flex-col gap-[8px]`}>
            <div className={`flex items-center justify-between`}>
              <div>
                { label }
              </div>
              <div>
                { progress }
              </div>
            </div>
            <div className={`h-2 rounded-full ${active ? 'bg-[#1780D6]' : 'bg-[#E5EDF6]'}`}>
              <div 
                style={{
                  width: progress as any
                }}
                className={`${active ? 'bg-white' : 'bg-[#CDD9E7]'} rounded-full h-full`} />
            </div>
          </div>
        ))
      }
    </div>
  );
}
