import { ChevronDownCircleIcon, ChevronDownIcon } from "lucide-react";
import React, { useState } from "react";
import { ThreeDotsSVG } from "../b64-images/ThreeDotsSVG";

export interface NDBOKRTableRowProps {
  id: string
  name: string
  pic: string
  progress: number
  ews: JSX.Element | string
  status: JSX.Element | string
  timeline: string
  list_key_result: NDOKRKeyResultData[]
  onAction?(id: string): void
  onObjectiveContextMenu?(x: number, y: number, id: string): void
  onKeyResultContextMenu?(x: number, y: number, id_objective: string, id: string): void
}

export function NDBOKRTableRow(props: NDBOKRTableRowProps) {
  const [open, setOpen] = useState<boolean>(false);

  return (
    <div className={`flex flex-col border-t border-t-[2px] border-t-[#EAEFF5]`}>
      <div className={`relative flex items-center p-4 gap-3 text-[#121D26] bg-white font-medium text-[14px]`}>

        {/* Single line */}
        {open && <div className={`w-px bg-[#0066B9] top-[50%] left-[28px] h-[50%] absolute`} />}

        <div className={`flex-[1] flex items-center gap-2`}>
          <div
            onClick={() => setOpen(!open)}
            className={`bg-white z-[2] cursor-pointer`}>
            <ChevronDownCircleIcon size={24} className={`text-[#0066B9] rounded-full ${open ? 'rotate-180' : ''}  transition transition-transform`} />
          </div>
          <div>
            { props.id }
          </div>
        </div>
        <div className={`flex-[2]`}>
          { props.name }
        </div>
        <div className={`flex-[2]`}>
          { props.pic }
        </div>
        <div className={`flex-[2] flex gap-2 items-center`}>
          <div className={`h-[8px] rounded-full bg-[#EDF1F4] flex-1`}>
            <div
              style={{
                width: `${Math.round(props.progress * 100)}%`
              }}
              className={`bg-[#0066B9] rounded-full h-full`} />
          </div>
          <div className={`text-[#0066B9] font-medium`}>{Math.round(props.progress * 100)}%</div>
        </div>
        <div className={`flex-[2]`}>
          { props.timeline }
        </div>
        <div className={`flex-[3]`}>
          { props.ews }
        </div>
        <div className={`flex-[1]`}>
          { props.status }
        </div>
        <div className={`max-w-[60px] min-w-[60px] w-[60px] flex justify-center`}>
          <img 
            onClick={e => {
              e.stopPropagation();
              props.onObjectiveContextMenu && props.onObjectiveContextMenu(e.pageX, e.pageY, props.id);
            }}
            className={`h-[20px] w-[20px] object-contain cursor-pointer`}
            src={ThreeDotsSVG} />
        </div>
      </div>

      <div className={`flex flex-col ${open ? 'max-h-[999px]' : 'max-h-[0px]'} overflow-y-hidden transition transition-all`}>
        {
          props.list_key_result.map((kr, j: number) => {
            const last_item = j == (props.list_key_result.length - 1);
            return (
              <KeyResultItem 
                data={kr}
                key={j}
                lastItem={last_item}
                idObjective={props.id}
                onKeyResultContextMenu={props.onKeyResultContextMenu} />
            );
          })
        }
      </div>
    </div>
  );
}

export interface NDOKRKeyResultData {
  id: string
  name: string
  pic: string
  progress: number
  ews: JSX.Element | string
  timeline: string
  status: JSX.Element | string
  onAction?(id: string): void
}

interface KeyResultItemProps {
  data: NDOKRKeyResultData
  idObjective: string
  lastItem?: boolean
  onKeyResultContextMenu?(x: number, y: number, id_objective: string, id: string): void
}
function KeyResultItem(props: KeyResultItemProps) {
  return (
    <div className={`flex flex-col bg-[#F0F5F9] border-t border-t-[2px] border-t-[#EAEFF5]`}>
      <div className={`relative flex items-center p-4 py-2 gap-3 text-[#121D26] text-[14px]`}>

        {/* Single line */}
        <div className={`w-px bg-[#0066B9] top-0 left-[28px] absolute ${props.lastItem ? 'h-[50%]' : 'h-full'}`} />

        <div className={`flex-[1] flex items-center gap-2`}>
          <div className={`z-[2] w-[16px] h-[24px] flex items-center ml-[12px]`}>
            <div className={`w-[16px] h-px bg-[#0066B9]`} />
            <div className={`min-w-[6px] min-h-[6px] w-[6px] h-[6px] bg-[#0066B9] rounded-[3px]`} />
          </div>
          <div className={`text-[#A1AFC0]`}>
            { props.data.id }
          </div>
        </div>
        
        <div className={`flex-[2]`}>
          { props.data.name }
        </div>
        <div className={`flex-[2]`}>
          { props.data.pic }
        </div>
        <div className={`flex-[2] flex gap-2 items-center`}>
          <div className={`h-[6px] rounded-full bg-[#EDF1F4] flex-1`}>
            <div
              style={{
                width: `${Math.round(props.data.progress * 100)}%`
              }}
              className={`bg-[#0066B9] rounded-full h-full`} />
          </div>
          <div className={`text-[#0066B9] font-medium`}>{Math.round(props.data.progress * 100)}%</div>
        </div>
        <div className={`flex-[2]`}>
          { props.data.timeline }
        </div>
        <div className={`flex-[3]`}>
          { props.data.ews }
        </div>
        <div className={`flex-[1]`}>
          { props.data.status }
        </div>
        <div className={`max-w-[60px] min-w-[60px] w-[60px] flex justify-center`}>
          <img 
            onClick={e => {
              e.stopPropagation();
              props.onKeyResultContextMenu && props.onKeyResultContextMenu(e.pageX, e.pageY, props.idObjective, props.data.id);
            }}
            className={`h-[20px] w-[20px] object-contain cursor-pointer`}
            src={ThreeDotsSVG} />
        </div>
      </div>
    </div>
  );
}
